.service__name{
  background-color: #283645;
  border: 2px solid #283645;
  border-radius: 1px 0px 0px 0px;

}

.service__title{
  text-align: center;
  font-size:16px;
  line-height: 30px;
  font-weight: 700;
  color:#ffffff;
}

.service__price{
  display: flex;
  justify-content: center;
  color:#ffffFf;
  font-size:18px;
  line-height: 30px;
  font-weight: 300;
}

.service__item{
  text-align: center;
  line-height: 30px;
  font-weight: 400;
  text-transform: uppercase;
}
