html{
width: 100%;
height: 100%;
}
body{
width: 320px;
margin: 0 auto;
background-image: url("../img/back-mobile.jpg");
background-position:50% 0%;
background-repeat: no-repeat;
font-family: "Open Sans", "Arial", sans-serif;
font-size: $font;
color: $text-color;
line-height: $line-height;
}

.visually-hidden{
position: absolute;
width: 1px;
height: 1px;
margin: -1px;
padding: 0;
border: 0;
clip: rect(0 0 0 0);
overflow: hidden;
}
